import React from 'react';
import ReactDOM from 'react-dom';

import moment from 'moment'
import './index.css';
import banner from './banner.jpg';
import about from './about.png';
import auction from './auction.png';
import calendar from './calendar.png';
import contact from './contact.png';
import phone from './phone.png';
import email from './email.png';
import location from './location.png';
import action1 from './action1.PNG';
import action2 from './action2.jpg';
import couple from './couple.jpg';
import profile from './profile.jpg';
import coverImage from './coverImage.jpg'
import "react-big-calendar/lib/css/react-big-calendar.css"
import {getEvents} from './gcal'

import BigCalendar from 'react-big-calendar'
// a localizer for BigCalendar
BigCalendar.momentLocalizer(moment)


class Cal extends React.Component {
  constructor () {
    super()
    this.state = {
      events: []
    }
  }
  componentDidMount () {
    getEvents((events) => {
      this.setState({events})
    })
  }
  render () {
    return (
      // React Components in JSX look like HTML tag
      <BigCalendar
      style={{height: '420px', font:'Roboto'}}
              events={this.state.events}
                 
              defaultDate={moment().toDate()}
      />
    )
  }
}


// ========================================

// Navigation bar

 

class Square extends React.Component {

  constructor(props) {

    super(props);

    this.state = { value: props.image, showText:false, color:'#afcaff'}

    this.hoverOn= this.hoverOn.bind(this)

    this.hoverOff= this.hoverOff.bind(this)

    this.color 

  }

 

  render() {

    return (

      <button className="square" 
        onClick={this.props.click}
        onMouseOver={this.hoverOn} onMouseOut={this.hoverOff} >
        <div>
            <div hidden={!this.state.showText}>
                {this.state.value} 
            </div>
            <img src={this.props.image} height="30px" width="30px" hidden={this.state.showText} alt={this.props.text}/>
        </div>
      </button>

    );

  }

 

  hoverOn() {
    this.setState({ value: this.props.text, showText:true, color:"#080350"});
  }

 

  hoverOff() {
    this.setState({ value: this.props.text, showText:false, color:"#fff"});
  }

}

 

class Board extends React.Component {

  render() {

    const message = '';

    return (

      <div>

        <div className="board-row">

          {<Square text='About' image={about} click={this.props.showAbout}/>}

          {<Square text='Services' image={auction} click={this.props.showServices}/>}

          {<Square text='Contact' image={contact} click={this.props.showContact}/>}

          {<Square text='Calendar' image={calendar}  click={this.props.showCalendar}/>}

        </div>

      </div>

    );

  }

}

 

// ========================================

// Page

 

class Page extends React.Component {

  constructor(props) {

    super(props);

    this.state = { about: true,

                   calendar: true,

                   services: true,

                   contact: true, 
                  
                   hideCover:false,
                  }

    this.showAbout= this.showAbout.bind(this)

    this.showCalendar= this.showCalendar.bind(this)

    this.showContact= this.showContact.bind(this)

    this.showServices= this.showServices.bind(this)

    this.displayCover= this.displayCover.bind(this)

  }

 

  showAbout() {

     this.setState ({ about: false,

                   calendar: true,

                   services: true,

                   contact: true, 
                  
                   hideCover:true});

  }

 

  showCalendar() {

     this.setState ({ about: true,

                   calendar: false,

                   services: true,

                   contact: true, 
                  
                   hideCover:true});

  }

 

  showServices() {

     this.setState ({ about: true,

                   calendar: true,

                   services: false,

                   contact: true, 
                  
                   hideCover:true,});

  }

 

  showContact() {

     this.setState ({ about: true,

                   calendar: true,

                   services: true,

                   contact: false, 

                  hideCover:true, });

  }

 
  displayCover () {
    this.setState({ about: true,

      calendar: true,

      services: true,

      contact: true, 

     hideCover:false, });
  }

  render() {

    const paragraph = 'Ken grew up near Sharon Center, Ohio in Medina County. His first job was at the age of 14 washing dishes on weekends at a local Medina restaurant, Mozena\'s. After high school, Ken joined the Marine Corps and served four years, earning promotions to Corporal (E-4) before being honorably discharged. After that, Ken attended The University of Akron, majoring in Business. He has since worked more that 20 years in Columbus law firm accounting departments. While working full-time, he attended Hondros College Real Estate courses and The Ohio Auction School to obtain Real Estate and Auctioneer licenses. Ken is passionate about assisting people with real estate and auction services. In his free time, he enjoys spending time with his family in Grove City.';

    const header = 'Ken Beckwith'

    const phoneNumber= '614-327-6820'

    const realEstate = 'Ken received his real estate license from Hondros College and is a realtor with Coldwell Banker. Check out his website '

    const collectibles = 'Southwest Auctions provide a variety of services, including personal and business property sales, estate cleanups, home tag sales, and online auction sales.'

    const fundraisers = 'Ken enjoys auctioneering for a variety of charitable causes. His experience includes church and youth fundraising events.'

    return (
      <div className="mainWrapper">
        <div className="bannerDiv">
  
        {/*<img className="headbanner" src={banner} float="left" height="110px"  width="100%" hidden={!this.state.hideCover}/>
          
          <div className="boardWrapper" >*/}
          <button className="homeButton" onClick={this.displayCover}>
          Ken Beckwith | Home
          </button>

          <div className="end">
            </div>
              <Board showCalendar={this.showCalendar} showAbout={this.showAbout}

                  showContact={this.showContact} showServices={this.showServices}/>
            <div className="end">
          </div>

        </div>
        
      <img className="coverPage" src={coverImage} float="left" width="100%" hidden={this.state.hideCover}/>


      <div className="mainPage">

      <div className="aboutPage" id="about page" hidden= {this.state.about} >
      
          <img className="aboutPhoto" src={profile} />
          <h1 className="header"> Ken Beckwith </h1>
          <h2 className="h2" > Auctioneer, Realtor </h2> 
          <div display="block">
            <div className="p1"  >
              <p >
              {paragraph} 
              </p>
            </div>
          </div>
      </div>

        <div id="calendar page" hidden= {this.state.calendar} >

          <h1 className="header"> Upcoming Events </h1>
          <div className="calendarPage">
          <Cal />
          </div>
         {/*<iframe src="https://calendar.google.com/calendar/b/1/embed?showTitle=0showCalendars=0&amp;height=600&amp;font-family=Lato;wkst=1&amp;bgcolor=%23999999&amp;src=kenbeckwithauctions%40gmail.com&amp;color=%231B887A&amp;ctz=America%2FNew_York" font="Vollkorn" font-color="black" border-width="0" width="800" height="500" frameborder="0" scrolling="no"></iframe>
*/}
        
      </div>

        <div id="contact page" hidden= {this.state.contact} >
 
          <h1 className="header"> Contact </h1>
          <div className="contactpage">
            <div className="contactinfo" >
              <div className="contactitem"> 
                <img className="element" src={phone} height="20px" width="20px" float="left" alt="phone number"/>
                {phoneNumber}
              </div>
              <div className="contactitem">
                <img className="element" src={email} height="20px" width="20px" float="left" alt="email link"/>
                <a href="mailto:kenabeckwith@gmail.com">kenabeckwith@gmail.com</a> 
              </div>
              <div className="contactitem">
                <img className="element" src={location} height="20px" width="20px" float="left" alt="location"/>
                <a href="https://goo.gl/maps/tmwvKBiVr6r">Grove City, Ohio</a> 
              </div>
              {/*<div>
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d97998.14159143167!2d-83.13496126256516!3d39.86433793810394!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x883884a4f49dedcf%3A0xc046ac1a22976938!2sGrove+City%2C+OH!5e0!3m2!1sen!2sus!4v1527484653937" width="300" height="225" frameborder="0" border="0" allowfullscreen></iframe>
              </div>*/}
            </div>
            <div className="contactParagraph">
              <p>
                Please reach out with any questions through email, phone or the contact form below.  
              </p>
            </div>
            <div className="contactForm">
              < ContactForm />
            </div>
          </div>

      </div>

        <div id="services page" hidden= {this.state.services} >     
          <h1 className="header"> Services </h1>
          <div className="servicedescription"> 
          Ken provides real estate and auction services. At Southwest Auctions, Ken and his wife Laura work together to deliver great auction services of all kinds. 
          They look forward to hearing about your specific auction needs. 
            </div>
          <div className="parent servicePage">    
            <div className="service" >
              <img className="action" text="test" src={action2} height="100px" width="150px"  alt="location"/>
              <h2 className="header2 serviceheader"> Real Estate             </h2>
              <div className="servicep"> 
              {realEstate}
              <a href="https://www.coldwellbankerhomes.com/oh/grove-city/agent/ken-beckwith/aid_180647/" target="_blank" rel="noopener noreferrer">here.</a> 
              </div>
            </div>
            <div className=" service centerItem" display="flex" align-items="center">
              <img className="action" text="test" src={couple} height="100px" width="150px" alt="location"/>
              <h2 className="header2 middleheader serviceheader" margin-top="0"> Southwest Auctions </h2>
              <div className="servicep"> {collectibles} </div>
            </div>
            <div className=" service" display="flex" align-items="center" >
              <img className="action" text="test" src={action1} height="100px" width="150px" alt="location"/>
              <h2 className="header2 serviceheader"> Fundraising             </h2>
              <div className="servicep"> {fundraisers} </div>
            </div>
          </div>
      </div>

      </div>

      <div className="footer">
              <div className="copyright" > 
              Cover photo courtesy of Jordan Griffith at <a href="https://jpgphotos.weebly.com/"> jpgphotography</a>
              <br />
              Copyright © 2018 Southwest Auctions
               </div>
      </div> 
      </div>
    );

  }

}
 
// ========================================

 

// Contact form

 

class ContactForm  extends React.Component {
 

  onChange() {}

 

  onSubmit() {}

 

  render() {

    return (

      <div>

        <form className="contactForm" action="https://formspree.io/kenbeckwithauctions@gmail.com"

               method="POST">

          <input type="text" className="form Error name" placeholder="Name" name="name" >        

          </input>

          <input type="email" className="form Error email" placeholder="Email" name="_replyto">

          </input>

          <input type="textarea" className="form Error message" placeholder="Message" name="message">

          </input>

          <button type="submit" className="form submit" value="Send"> Submit </button>

        </form>

        </div>

    );

  }

}

 

// ========================================

 

ReactDOM.render(

  <Page />,

  document.getElementById('root')

);


